@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  cursor: pointer;
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
}
.button:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.button:active {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.header-container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 5rem;
  background-color: rgb(236, 181, 93);
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Roboto", sans-serif;
  padding: 2rem;
}

.logo {
  cursor: pointer;
  color: #2984a5;
  width: auto;
}
.logo:active {
  background-color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.logo:hover {
  color: rgba(255, 255, 255, 1);
}

.avatar {
  display: -webkit-flex;
  display: flex;
  width: auto;
  height: 2.5rem;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: row;
          flex-direction: row;
  cursor: pointer;
  padding: 0.5rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.avatar:active {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0 0 5px -1px rgba(0.2, 0.2, 0.2, 0.8);
}

.icon {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right: 1rem;
  color: #2984a5;
  font-weight: bold;
}

.username {
  color: #2984a5;
  font-size: 100;
  font-weight: bold;
}

.username:hover {
  color: rgba(255, 255, 255, 1);
}

.spinner-container {
  width: 100%;
  height: 97vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.spinner {
  -webkit-animation: spin infinite 5s linear;
          animation: spin infinite 5s linear;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.errors {
  color: red;
  font-weight: bold;
}

